$color: #ff6060;
$grey: #f6f6f6;
$radius: 10px;
$radiusLarge: 20px;
$radiusSmall: 5px;
$breakpoints: (
    tablet: 768px,
    desktop: 992px
);

@mixin tablet {
  @media screen and (min-width: map-get($breakpoints, tablet)) {@content}
};

